// 导入 store 才可以修改里面的属性，若在计算属性中用 mapState 解构的是只读不可修改的 
import store from '@/store/index.js';
import { getSearchSinger } from '@/request/api/search.js';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 搜索的关键词
      searchKey: '',
      // 传给接口的页码
      num: 1,
      // 接收的歌手们
      artists: [],
      // 零
      zero: 0,
      // 查到的歌手数量
      artistCount: 0,
      // html渲染的歌手
      vHtmlSinger: '',
      // 是否显示 v-html 嵌入的歌手关键字 html标签
      isVHtmlSinger: 0
    };
  },

  async mounted() {
    // 获取关键词
    this.searchKey = store.state.searchKey; // 搜索歌手
    // let res = await getSearchSinger(store.state.searchKey, this.num)

    if (sessionStorage.getItem('searchKey')) {
      let res = await getSearchSinger(sessionStorage.getItem('searchKey'), this.num); // console.log('搜索歌手', res);

      this.artists = res.data.result.artists;
      this.artistCount = res.data.result.artistCount;
      if (this.artists.length !== 0) this.isLoading = 0; // console.log(this.artists);
    }
  },

  methods: {
    // 歌手关键词高亮显示
    activeSinger(string) {
      let reg = new RegExp(this.searchKey, 'g');
      let replaceString = '<span style="color: rgb(27, 94, 195)">' + this.searchKey + '</span>';
      string = string.replace(reg, replaceString);

      if (string.indexOf(this.searchKey) == -1) {
        this.isVHtmlSinger = 0;
        return string;
      } else {
        this.isVHtmlSinger = 1;
        this.vHtmlSinger = string;
        return '';
      }
    },

    // 去往歌手页
    goSingerPage(id) {
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    }

  },
  watch: {// 因为每新搜索一次都会跳往单曲栏，所以本栏不用监听关键词
    // searchKey() {
    //   this.isLoading = 1
    // }
  }
};